import React from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { CheckCircle2, ArrowDownLeft, ArrowUpRight, Wallet } from 'lucide-react';
import { Transaction } from '@/types/Transaction'; // Adjust import path if needed

interface AutoTransactionDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  transaction: any; // Using any for flexibility with Transaction type
  walletName?: string;
}

export function AutoTransactionDialog({ 
  open, 
  onOpenChange, 
  transaction,
  walletName 
}: AutoTransactionDialogProps) {
  if (!transaction) return null;

  const isReceive = transaction.type === 'receive' || transaction.type === 'withdraw'; // withdraw in system usually means receive from perspective of wallet? Wait, 'withdraw' in Dashboard is usually adding to system/withdraw from wallet. 
  // Let's check dashboard logic: withdraw = red icon (ArrowDownLeft), send = green icon (ArrowUpRight).
  // But for "Receive Money" (SMS receive), it increases balance.
  // In DashboardPage: 
  // transaction.type === 'withdraw' ? 'bg-red-50' (ArrowDownLeft)
  // transaction.type === 'send' ? 'bg-green-50' (ArrowUpRight)
  // User says: "receive transaction... received amount X".
  // In SmsReceiver: txnType="receive" -> increases totalWithdrawn? Wait.
  // SmsReceiver: if (txnType.equals("send")) { statsUpdates.put("totalSent"...) } else { statsUpdates.put("totalWithdrawn"...) }
  // This naming is confusing. "Withdraw" usually means taking money OUT of the wallet? Or OUT of the system?
  // In `DashboardPage`: 'withdraw' -> '+' (Add to system/cash), 'send' -> '-' (Remove from system/cash).
  // So 'receive' SMS (money comes into wallet) -> likely maps to 'withdraw' (add to system/cash balance if we track wallet balance as system cash? No).
  // Let's stick to the visual cues:
  // If we RECEIVED money (SMS), it should be Positive.
  // In DashboardPage: `transaction.type === 'send' ? '-' : '+'`.
  // So 'withdraw' means Positive (Receive).
  
  const isIncoming = transaction.type === 'withdraw' || transaction.type === 'receive';
  const amount = Math.abs(transaction.amount || 0);
  
  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="sm:max-w-md border-0 shadow-2xl overflow-hidden p-0 gap-0">
        {/* Header Background */}
        <div className={`h-32 w-full flex flex-col items-center justify-center ${
          isIncoming ? 'bg-emerald-600' : 'bg-blue-600'
        }`}>
          <div className="bg-white/20 p-3 rounded-full mb-2 backdrop-blur-sm">
            <CheckCircle2 className="w-10 h-10 text-white" />
          </div>
          <h2 className="text-white text-xl font-bold">
            {isIncoming ? 'تم استلام مبلغ بنجاح' : 'تم التحويل بنجاح'}
          </h2>
        </div>

        <div className="p-6 space-y-6">
          {/* Amount */}
          <div className="text-center">
            <p className="text-sm text-gray-500 font-medium mb-1">المبلغ</p>
            <div className={`text-4xl font-bold ${isIncoming ? 'text-emerald-600' : 'text-blue-600'}`}>
              {amount.toLocaleString()} <span className="text-xl text-gray-400 font-normal">ج.م</span>
            </div>
          </div>

          {/* Details Card */}
          <div className="bg-gray-50 rounded-xl p-4 space-y-3 border border-gray-100">
            {/* Sender/Receiver */}
            <div className="flex justify-between items-center">
              <span className="text-sm text-gray-500">
                {isIncoming ? 'من' : 'إلى'}
              </span>
              <span className="font-semibold text-gray-900" dir="ltr">
                {isIncoming ? (transaction.senderName || transaction.senderPhone || transaction.senderMsisdn) : (transaction.receiverName || transaction.receiverPhone || transaction.receiverMsisdn)}
              </span>
            </div>

            <div className="h-px bg-gray-200 w-full" />

            {/* Wallet */}
            <div className="flex justify-between items-center">
              <span className="text-sm text-gray-500 flex items-center gap-1">
                <Wallet className="w-3 h-3" />
                المحفظة
              </span>
              <span className="font-semibold text-gray-900">
                {walletName || transaction.shopName || 'غير محدد'}
              </span>
            </div>

            <div className="h-px bg-gray-200 w-full" />

            {/* Reference */}
            <div className="flex justify-between items-center">
              <span className="text-sm text-gray-500">رقم العملية</span>
              <span className="font-mono text-xs text-gray-600 bg-gray-200 px-2 py-1 rounded">
                {transaction.reference || transaction.id}
              </span>
            </div>
          </div>

          <Button 
            onClick={() => onOpenChange(false)} 
            className={`w-full py-6 text-lg shadow-lg hover:shadow-xl transition-all ${
              isIncoming ? 'bg-emerald-600 hover:bg-emerald-700' : 'bg-blue-600 hover:bg-blue-700'
            }`}
          >
            تم
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  );
}
