import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Badge } from '@/components/ui/badge';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { ArrowUpDown, Plus, CheckCircle, Clock, XCircle, Phone, DollarSign, Wallet, ArrowDownLeft, Trash2, Edit, LogOut, Settings, Eye } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';
import { useFirebaseAuth } from '@/contexts/FirebaseAuthContext';
import WalletManagement from '@/components/WalletManagement';
import TransactionDetailsModal from '@/components/TransactionDetailsModal';
import { SecretPinDialog } from '@/components/SecretPinDialog';
import { EditBalanceDialog } from '@/components/EditBalanceDialog';
import { ChangePinDialog } from '@/components/ChangePinDialog';
import { AddToWalletDialog } from '@/components/AddToWalletDialog';
import { transactionService, profileService, merchantWalletService } from '@/integrations/firebase/services';
import { AutoTransactionDialog } from '@/components/AutoTransactionDialog';
import { doc, updateDoc, getDoc, setDoc, getDocFromCache, collection, query, where, orderBy, limit, onSnapshot } from 'firebase/firestore';
import { db } from '@/firebase';
import { useShift } from '@/contexts/ShiftContext';
import { MasterPinDialog } from '@/components/MasterPinDialog';
import { useIsMobile } from '@/hooks/use-mobile';
import { Capacitor } from '@capacitor/core';
import { calculateCommission } from '@/utils/commissionCalculator';
import { ProfitService } from '@/utils/profitService';

interface Transaction {
  id: string;
  type: 'send' | 'receive' | 'withdraw';
  senderPhone: string;
  receiverPhone: string;
  amount: number;
  status: 'pending' | 'completed' | 'failed';
  createdAt: Date;
  withdrawnAmount?: number;
  sentAmount?: number;
  merchantShopName?: string;
  shopName?: string;
  commission?: number;
  fee?: number;
  fromWallet?: string;
  provider?: string;
  senderName?: string;
  cashierName?: string;
}

interface DailyStats {
  date: string;
  totalTransactions: number;
  totalAmount: number;
  totalWithdrawn: number;
  totalSent: number;
}

interface MonthlyStats {
  month: string;
  totalTransactions: number;
  totalAmount: number;
  totalWithdrawn: number;
  totalSent: number;
}

const DashboardPage = () => {
  const location = useLocation();
  const { toast } = useToast();
  const { signOut, user, profile } = useFirebaseAuth();
  const { isShiftActive, shiftUser } = useShift();
  const isMobile = useIsMobile();
  const platform = (typeof Capacitor !== 'undefined' && typeof Capacitor.getPlatform === 'function') ? Capacitor.getPlatform() : 'web';
  const isAndroid = platform === 'android' || /Android/i.test(typeof navigator !== 'undefined' ? navigator.userAgent : '');
  // تفعيل تخطيط الموبايل/الأندرويد: عرض آخر 3 فقط والباقي في قسم قابل للتمرير
  const showAndroidMobileLayout = isAndroid || isMobile;
  const [senderPhone, setSenderPhone] = useState('01030302038');
  const [senderName, setSenderName] = useState('');
  const [receiverPhone, setReceiverPhone] = useState('');
  const [amount, setAmount] = useState('');
  const [transactionType, setTransactionType] = useState<'transfer' | 'withdraw'>('transfer');
  const [senderProvider, setSenderProvider] = useState<'vodafone_cash' | 'etisalat_cash' | 'orange_cash' | 'instapay'>('vodafone_cash');
  const [receiverProvider, setReceiverProvider] = useState<'vodafone_cash' | 'etisalat_cash' | 'orange_cash' | 'instapay'>('vodafone_cash');
  const [transferFee, setTransferFee] = useState('');
  const [transactions, setTransactions] = useState<Transaction[]>([]);
  const [activeTab, setActiveTab] = useState('all');
  const [isWalletManagementOpen, setIsWalletManagementOpen] = useState(false);
  const [selectedWallet, setSelectedWallet] = useState<string>('');
  const [wallets, setWallets] = useState<Array<{id: string, name: string, phone: string, isDefault: boolean}>>([]);
  const [selectedTransaction, setSelectedTransaction] = useState<Transaction | null>(null);
  const [isTransactionDetailsOpen, setIsTransactionDetailsOpen] = useState(false);
  const [dailyStats, setDailyStats] = useState<DailyStats[]>([]);
  const [monthlyStats, setMonthlyStats] = useState<MonthlyStats[]>([]);
  const [isResetDialogOpen, setIsResetDialogOpen] = useState(false);
  const [resetType, setResetType] = useState<'daily' | 'monthly'>('daily');
  const [deletedTransactions, setDeletedTransactions] = useState<Transaction[]>([]);
  const MONTHLY_WITHDRAWAL_LIMIT = 200000;
  const MONTHLY_TRANSFER_LIMIT = 200000;
  const [cashBalance, setCashBalance] = useState(0);
  const [walletBalances, setWalletBalances] = useState<{[key: string]: number}>({});
  const [isEditCashDialogOpen, setIsEditCashDialogOpen] = useState(false);
  const [isEditWalletDialogOpen, setIsEditWalletDialogOpen] = useState(false);
  const [isAddToWalletDialogOpen, setIsAddToWalletDialogOpen] = useState(false);
  const [isAddToWalletTotalDialogOpen, setIsAddToWalletTotalDialogOpen] = useState(false);
  const [isEditWalletTotalDialogOpen, setIsEditWalletTotalDialogOpen] = useState(false);
  const [editingWalletId, setEditingWalletId] = useState<string>('');
  const [isChangePinDialogOpen, setIsChangePinDialogOpen] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [isDailyLocked, setIsDailyLocked] = useState<boolean>(() => {
    try {
      const key = user?.uid ? `dailyReportsLockEnabled_${user?.uid}` : 'dailyReportsLockEnabled';
      const v = localStorage.getItem(key) ?? localStorage.getItem('dailyReportsLockEnabled');
      return v === 'true';
    } catch {
      return false;
    }
  });
  const [isDailyPinDialogOpen, setIsDailyPinDialogOpen] = useState<boolean>(false);
  const [isTransactionPinned, setIsTransactionPinned] = useState(false);
  const [autoTransaction, setAutoTransaction] = useState<Transaction | null>(null);
  const lastTxIdRef = React.useRef<string>('');

  // Load Wallets for Name Resolution
  useEffect(() => {
    if (!user?.uid) return;
    const fetchWallets = async () => {
      try {
        const ws = await merchantWalletService.getByMerchantId(user.uid);
        if (ws) {
          const normalized = ws.map((w: any) => ({
            id: w.id,
            name: w.label || w.alias || 'محفظة',
            phone: w.msisdn || '',
            isDefault: false
          }));
          setWallets(normalized);
        }
      } catch (e) { console.error('Failed to load wallets', e); }
    };
    fetchWallets();
  }, [user?.uid]);

  // Real-time listener for Desktop Notifications & Auto-Updates
  useEffect(() => {
    if (!user || isMobile || isAndroid) return;
    
    let unsub = () => {};
    
    const setupListener = async () => {
      const p = await profileService.getById(user.uid);
      if (p?.shopId) {
        const dataRef = doc(db, 'dashboardData', p.shopId);
        unsub = onSnapshot(dataRef, (snap) => {
          if (snap.exists()) {
            const data = snap.data();
            const txs = (data.transactions || []) as Transaction[];
            
            // Check for new Auto Transaction
            if (txs.length > 0) {
              const latest = txs[0];
              // Initialize ref if empty
              if (!lastTxIdRef.current) {
                lastTxIdRef.current = latest.id;
              } else if (lastTxIdRef.current !== latest.id) {
                // New transaction detected
                // Check if it's likely an Auto/SMS transaction
                // (has provider set, or comes from background sync)
                const isAuto = (latest as any).provider || 
                               (latest as any).originType === 'transfer' || 
                               (latest as any).originType === 'withdraw' ||
                               (latest as any).originType === 'sms' ||
                               (latest as any).originType === 'auto' ||
                               latest.reference?.startsWith('VF-');
                
                if (isAuto) {
                  setAutoTransaction(latest);
                }
                lastTxIdRef.current = latest.id;
              }
            }
            
            // Update Dashboard Data Real-time (restore functionality)
            setTransactions(txs);
            
            // Fix Balance Flicker: Only update if valid numbers, fallback to existing or localStorage
            // This prevents momentary 0 balance when Firestore returns partial/empty snapshots during sync
            const newCash = data.cashBalance;
            if (typeof newCash === 'number') {
               setCashBalance(newCash);
               try { localStorage.setItem('lastKnownCashBalance', String(newCash)); } catch {}
            }
            
            const newWallets = data.walletBalances;
            if (newWallets && Object.keys(newWallets).length > 0) {
               setWalletBalances(newWallets);
               try { localStorage.setItem('lastKnownWalletBalances', JSON.stringify(newWallets)); } catch {}
            }
            
            setDeletedTransactions(data.deletedTransactions || []);
          }
        });
      }
    };
    
    setupListener();
    return () => unsub();
  }, [user, isMobile, isAndroid]);

  // Initial Local Storage Load to prevent 0 flicker on mount
  useEffect(() => {
     try {
       const cachedCash = localStorage.getItem('lastKnownCashBalance');
       if (cachedCash && !isNaN(Number(cachedCash))) {
          setCashBalance(Number(cachedCash));
       }
       const cachedWallets = localStorage.getItem('lastKnownWalletBalances');
       if (cachedWallets) {
          setWalletBalances(JSON.parse(cachedWallets));
       }
     } catch {}
  }, []);

  // كشف الشركة تلقائيًا من مقدمة الرقم
  // Helper: normalize Arabic/Persian digits to ASCII
  const normalizeDigits = (s: string) => {
    const arabicIndic = '٠١٢٣٤٥٦٧٨٩';
    const easternIndic = '۰۱۲۳۴۵۶۷۸۹';
    return (s || '').split('').map(ch => {
      const ai = arabicIndic.indexOf(ch);
      if (ai > -1) return String(ai);
      const ei = easternIndic.indexOf(ch);
      if (ei > -1) return String(ei);
      return ch;
    }).join('');
  };

  const detectProvider = (phone: string): 'vodafone_cash' | 'etisalat_cash' | 'orange_cash' | 'instapay' | null => {
    const digits = normalizeDigits(phone).replace(/\D/g, '');
    if (digits.startsWith('010')) return 'vodafone_cash';
    if (digits.startsWith('011')) return 'etisalat_cash';
    if (digits.startsWith('012')) return 'orange_cash';
    // مبدئيًا لا ندعم 015 (WE) هنا؛ تظل القيمة كما هي أو يُستخدم الاختيار اليدوي
    return null;
  };

  useEffect(() => {
    const p = detectProvider(senderPhone);
    if (p && p !== senderProvider) setSenderProvider(p);
  }, [senderPhone]);

  useEffect(() => {
    const p = detectProvider(receiverPhone);
    if (p && p !== receiverProvider) setReceiverProvider(p);
  }, [receiverPhone]);

  useEffect(() => {
    try {
      const key = user?.uid ? `dailyReportsLockEnabled_${user?.uid}` : 'dailyReportsLockEnabled';
      const v = localStorage.getItem(key) ?? localStorage.getItem('dailyReportsLockEnabled');
      setIsDailyLocked(v === 'true');
    } catch {}
  }, [user?.uid]);

  // تفعيل نموذج المعاملة ووضع التثبيت عند القدوم من أزرار أسفل الموبايل
  useEffect(() => {
    const state: any = location.state || {};
    if (state?.openTransactionSection) {
      if (state?.transactionType === 'withdraw' || state?.transactionType === 'transfer') {
        setTransactionType(state.transactionType);
      }
      setIsTransactionPinned(true);
      if (state?.startNewTransaction) {
        setSenderPhone('01030302038');
        setReceiverPhone('');
        setAmount('');
        setTransferFee('');
      }
      // تأكد من ظهور النموذج أعلى الصفحة في الموبايل
      setTimeout(() => {
        window.scrollTo({ top: 0, behavior: 'smooth' });
      }, 50);
    }
  }, [location.state]);

  // دالة لحفظ البيانات في Firebase
  const saveDataToFirebase = async (data: any) => {
    if (!user) return;
    
    try {
      const userProfile = await profileService.getById(user.uid);
      if (userProfile?.shopId) {
        // 1. Update Dashboard Data (Primary for Desktop)
        const dataRef = doc(db, 'dashboardData', userProfile.shopId);
        // Enforce integer values for cashBalance
        if (data.cashBalance !== undefined) {
          data.cashBalance = Math.round(data.cashBalance);
        }
        await updateDoc(dataRef, data);

        // 2. Sync balances to Users Collection (Primary for Android Native/Sync)
        // This fixes the issue where Desktop manual transactions don't reflect on Android
        const updates: any = {};
        if (data.walletBalances) updates.walletBalances = data.walletBalances;
        if (data.cashBalance !== undefined) {
          updates.cashBalance = Math.round(data.cashBalance);
        }
        
        if (Object.keys(updates).length > 0) {
           const userRef = doc(db, 'users', user.uid);
           await updateDoc(userRef, updates);
        }
      }
    } catch (error) {
      console.error('Error saving to Firebase:', error);
      toast({
        title: "خطأ في الحفظ",
        description: "حدث خطأ أثناء حفظ البيانات في Firebase",
        variant: "destructive",
      });
    }
  };

  useEffect(() => {
    if (!user) return;
    
    const loadDataFromFirebase = async () => {
      setIsLoading(true);
      try {
        const userProfile = await profileService.getById(user.uid);
        if (userProfile?.shopId) {
          // تحميل الأرصدة من Firebase
          const dataRef = doc(db, 'dashboardData', userProfile.shopId);
          // أولاً: محاولة قراءة سريعة من الكاش لعرض البيانات فورًا
          try {
            const cachedSnap = await getDocFromCache(dataRef);
            if (cachedSnap.exists()) {
              const cached = cachedSnap.data() as any;
              setCashBalance(cached.cashBalance || 0);
              setWalletBalances(cached.walletBalances || {});
              setDeletedTransactions(cached.deletedTransactions || []);
              // بما أن لدينا بيانات معروضة من الكاش، ننهي حالة التحميل مبكرًا
              setIsLoading(false);
            }
          } catch {
            // الكاش غير متوفر أو غير مفعّل، نتابع لجلب الشبكة
          }
          const dataSnap = await getDoc(dataRef);
          
          if (dataSnap.exists()) {
            const data = dataSnap.data();
            
            // تحسين معالجة الأرصدة لتجنب التصفير الخاطئ
            const fbCash = data.cashBalance;
            if (typeof fbCash === 'number') {
              setCashBalance(fbCash);
              try { localStorage.setItem('lastKnownCashBalance', String(fbCash)); } catch {}
            } else {
               // إذا كانت القيمة غير موجودة في المستند، نحاول استرجاع آخر قيمة معروفة
               const lastKnown = localStorage.getItem('lastKnownCashBalance');
               if (lastKnown && !isNaN(Number(lastKnown))) {
                 setCashBalance(Number(lastKnown));
               } else {
                 setCashBalance(0);
               }
            }

            const fbWallets = data.walletBalances;
            if (fbWallets && typeof fbWallets === 'object') {
              setWalletBalances(fbWallets);
              try { localStorage.setItem('lastKnownWalletBalances', JSON.stringify(fbWallets)); } catch {}
            } else {
               const lastKnown = localStorage.getItem('lastKnownWalletBalances');
               if (lastKnown) {
                 setWalletBalances(JSON.parse(lastKnown));
               } else {
                 setWalletBalances({});
               }
            }
            
            setDeletedTransactions(data.deletedTransactions || []);
          } else {
            const defaultData = {
              walletBalances: {},
              deletedTransactions: []
            };
            await setDoc(dataRef, defaultData);
            setCashBalance(0);
            setWalletBalances(defaultData.walletBalances);
            setDeletedTransactions(defaultData.deletedTransactions);
          }
        }
      } catch (error) {
        console.error('Error loading data from Firebase:', error);
        
        // في حالة الخطأ، نحاول استرجاع آخر قيم معروفة بدلاً من التصفير
        let recovered = false;
        try {
          const lastCash = localStorage.getItem('lastKnownCashBalance');
          const lastWallets = localStorage.getItem('lastKnownWalletBalances');
          
          if (lastCash !== null && !isNaN(Number(lastCash))) {
            setCashBalance(Number(lastCash));
            recovered = true;
          }
          
          if (lastWallets) {
            setWalletBalances(JSON.parse(lastWallets));
            recovered = true;
          }
        } catch {}

        if (!recovered) {
          setCashBalance(0);
          setWalletBalances({});
        }
        
        setDeletedTransactions([]);
      } finally {
        setIsLoading(false);
      }
    };

    loadDataFromFirebase();
  }, [user]);

  useEffect(() => {
    const handleExternalUpdate = (event: any) => {
      const { cashBalance: newCash, walletBalances: newWallets, newTransaction, delta } = event.detail || {};
      
      // Handle Delta Update (Optimistic)
      if (delta) {
          const { amount, type, walletId, commission, totalDeducted } = delta;
          const amt = Number(amount || 0);
          const comm = Number(commission || 0);
          const totalDed = Number(totalDeducted || (amt + comm));
          
          setCashBalance(prev => {
              let change = 0;
              if (type === 'withdraw') {
                  change = -amt + comm;
              } else if (type === 'send' || type === 'transfer') {
                  change = amt + comm;
              } else if (type === 'atm_withdraw') {
                  change = amt;
              }
              return prev + change;
          });
          
          if (walletId) {
              setWalletBalances(prev => {
                  const currentVal = Number(prev[walletId] || 0);
                  let change = 0;
                  if (type === 'withdraw') {
                      change = amt;
                  } else if (type === 'send' || type === 'transfer') {
                      change = -amt;
                  } else if (type === 'atm_withdraw') {
                      change = -totalDed;
                  }
                  return { ...prev, [walletId]: currentVal + change };
              });
          }
      }

      if (typeof newCash === 'number') {
        setCashBalance(newCash);
      }
      
      if (newWallets && typeof newWallets === 'object') {
        setWalletBalances(newWallets);
      }
      
      if (newTransaction) {
        setTransactions(prev => {
          // Check if transaction already exists to avoid duplicates
          if (prev.some(t => t.id === newTransaction.id)) {
            return prev;
          }
          return [newTransaction, ...prev];
        });
        
        // Also update daily stats if needed, but primarily transactions list
        toast({
          title: "تم تسجيل عملية خارجية",
          description: "تم تحديث الأرصدة والسجلات بنجاح",
        });
      }
    };

    window.addEventListener('dashboard:external-update', handleExternalUpdate);
    return () => window.removeEventListener('dashboard:external-update', handleExternalUpdate);
  }, []);

  // استبدال الاشتراك اللحظي بآلية التحديث اليدوي لتقليل القراءات
  const refreshDashboardData = async () => {
    if (!user) return;
    setIsLoading(true);
    try {
      const userProfile = await profileService.getById(user.uid);
      if (userProfile?.shopId) {
        const dataRef = doc(db, 'dashboardData', userProfile.shopId);
        // تجاهل الكاش هنا لضمان الحصول على أحدث البيانات عند الطلب اليدوي
        const dataSnap = await getDoc(dataRef);
        
        if (dataSnap.exists()) {
          const data: any = dataSnap.data();
          setCashBalance(data.cashBalance || 0);
          setWalletBalances(data.walletBalances || {});
          setDeletedTransactions(data.deletedTransactions || []);
          toast({
            title: "تم تحديث البيانات",
            description: "تم تحميل أحدث البيانات من السيرفر بنجاح",
          });
        }
      }
    } catch (e) {
      console.error('Error refreshing dashboard data:', e);
      toast({
        title: "فشل التحديث",
        description: "حدث خطأ أثناء تحديث البيانات",
        variant: "destructive",
      });
    } finally {
      setIsLoading(false);
    }
  };

  // تمت إزالة الاشتراك اللحظي (onSnapshot) لتقليل تكاليف Firebase Reads
  // الاعتماد الآن على:
  // 1. التحميل الأولي (loadDataFromFirebase)
  // 2. التحديث المحلي المتفائل عند إجراء عمليات (optimistic UI updates)
  // 3. التحديث اليدوي عند الحاجة


  // تمت إزالة مزامنة التخزين المحلي: العرض والعمليات تعتمد على Firestore فقط

  // باقي الكود يبقى كما هو...
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (transactionType === 'transfer') {
      // Transfer logic
      if (!senderPhone || !receiverPhone || !amount) {
        toast({
          title: "خطأ",
          description: "يرجى ملء جميع الحقول المطلوبة",
          variant: "destructive",
        });
        return;
      }

      // تحديد اسم المحل من بروفايل المستخدم، مع بدائل آمنة
      const merchantShopName = profile?.shopName || user?.displayName || 'محل كاشي لينك';

      // محاولة تحديد اسم المحفظة المختارة أو الافتراضية
      const selectedWalletObj = wallets.find(w => w.id === selectedWallet) || wallets.find(w => w.isDefault);

      // تحقق من الرسوم بناءً على بادئات الأرقام (شرط 010 → 011)
      const fee = (transferFee && transferFee.trim() !== '') ? parseFloat(transferFee) : 0;

      // تحقق من توفر المحفظة والرصيد الكافي
      if (!selectedWalletObj) {
        toast({ title: "لا توجد محفظة", description: "يرجى اختيار محفظة لإتمام التحويل", variant: "destructive" });
        return;
      }
      const parsedAmount = Math.round(parseFloat(amount));
      const totalDebit = parsedAmount + fee;
      const currentWalletBalance = walletBalances[selectedWalletObj.id] || 0;
      if (currentWalletBalance < totalDebit) {
        toast({ title: "رصيد المحفظة غير كافٍ", description: `الرصيد الحالي ${currentWalletBalance} لا يكفي لخصم ${totalDebit}`, variant: "destructive" });
        return;
      }

      const transferCommission = calculateCommission(parsedAmount, 'send');
      const newTransaction: Transaction = {
        id: Date.now().toString(),
        type: 'send',
        senderPhone,
        receiverPhone,
        amount: parsedAmount,
        status: 'pending',
        createdAt: new Date(),
        withdrawnAmount: 0,
        sentAmount: parsedAmount,
        merchantShopName,
        shopName: selectedWalletObj?.name || 'المحفظة الرئيسية',
        commission: transferCommission,
        fee: fee,
        fromWallet: selectedWalletObj.id,
      };

      // لا نضيف اسم الكاشير في معاملات الحساب الرئيسي حتى لو كانت وردية نشطة

      const updatedTransactions = [newTransaction, ...transactions];
      setTransactions(updatedTransactions);

      // تحديث رصيد المحفظة (خصم مبلغ التحويل + الرسوم)
      const totalDebit2 = parsedAmount + fee;
      const walletId = selectedWalletObj?.id;
      const currentWalletBalance2 = walletId ? (walletBalances[walletId] || 0) : 0;
      const newWalletBalances = walletId ? { ...walletBalances, [walletId]: currentWalletBalance2 - totalDebit2 } : walletBalances;
      setWalletBalances(newWalletBalances);

      // لا نضيف الرسوم إلى الدرج عند الإنشاء — الإضافة تتم عند الإكمال فقط

      // حفظ في Firebase بدون تعديل الدرج الآن
      await saveDataToFirebase({ transactions: updatedTransactions, walletBalances: newWalletBalances });

      toast({
        title: "تم إرسال التحويل",
        description: "جاري معالجة التحويل...",
      });

      setTimeout(async () => {
        const completedTransaction = { ...newTransaction, status: 'completed' as const };
        const finalTransactions = [completedTransaction, ...transactions];
        setTransactions(finalTransactions);
        
        // عند الإكمال: إضافة الرسوم إلى الدرج ثم حفظ
        const completedFee = fee;
        const toAdd = Math.max(0, transferCommission) + (Number.isFinite(completedFee) ? completedFee : 0);
        const newCashBalanceOnComplete = cashBalance + toAdd;
        setCashBalance(newCashBalanceOnComplete);
        await saveDataToFirebase({ transactions: finalTransactions, cashBalance: newCashBalanceOnComplete });

        ProfitService.addProfit(toAdd);

        // toast({
        //   title: "تم التحويل بنجاح",
        //   description: `تم تحويل ${amount} جنيه إلى ${receiverPhone} وخصم عمولة ${commission} جنيه من الدرج`,
        // });
      }, 2000);

      setSenderPhone('01030302038');
      setReceiverPhone('');
      setAmount('');
      setTransferFee('');
    } else if (transactionType === 'withdraw') {
      if (!senderPhone || !amount) {
        toast({
          title: "خطأ",
          description: "يرجى ملء جميع الحقول المطلوبة",
          variant: "destructive",
        });
        return;
      }

      const merchantShopName = profile?.shopName || user?.displayName || 'محل كاشي لينك';
      const selectedWalletObj = wallets.find(w => w.id === selectedWallet) || wallets.find(w => w.isDefault);
      if (!selectedWalletObj) {
        toast({ title: "لا توجد محفظة", description: "يرجى اختيار محفظة لإتمام السحب", variant: "destructive" });
        return;
      }

      const parsedAmount = Math.round(parseFloat(amount));
      if (!Number.isFinite(parsedAmount) || parsedAmount <= 0) {
        toast({ title: "خطأ", description: "أدخل مبلغًا صحيحًا للسحب", variant: "destructive" });
        return;
      }

      const withdrawCommission = calculateCommission(parsedAmount, 'receive');
      const newTransaction: Transaction = {
        id: Date.now().toString(),
        type: 'withdraw',
        senderPhone,
        receiverPhone: '',
        amount: parsedAmount,
        status: 'pending',
        createdAt: new Date(),
        withdrawnAmount: parsedAmount,
        sentAmount: 0,
        merchantShopName,
        shopName: selectedWalletObj?.name || 'المحفظة الرئيسية',
        commission: withdrawCommission,
        fromWallet: selectedWalletObj.id,
        senderName: senderName || undefined,
      };

      const updatedTransactions = [newTransaction, ...transactions];
      setTransactions(updatedTransactions);

      await saveDataToFirebase({ transactions: updatedTransactions });

      toast({
        title: "تم إنشاء عملية السحب",
        description: "جاري معالجة السحب...",
      });

      setTimeout(async () => {
        const completedTransaction = { ...newTransaction, status: 'completed' as const };
        const finalTransactions = [completedTransaction, ...transactions];
        setTransactions(finalTransactions);

        const fromWalletId = selectedWalletObj.id;
        const currentWalletBalance = walletBalances[fromWalletId] || 0;
        const newWalletBalances = { ...walletBalances, [fromWalletId]: currentWalletBalance + parsedAmount };
        setWalletBalances(newWalletBalances);

        const newCashBalanceOnComplete = cashBalance - parsedAmount + Math.max(0, withdrawCommission);
        setCashBalance(newCashBalanceOnComplete);

        await saveDataToFirebase({ transactions: finalTransactions, walletBalances: newWalletBalances, cashBalance: newCashBalanceOnComplete });

        ProfitService.addProfit(Math.max(0, withdrawCommission));
      }, 2000);

      setSenderPhone('01030302038');
      setAmount('');
    }
  };

  return (
    <div className="min-h-screen bg-background p-4">
      <AutoTransactionDialog 
        open={!!autoTransaction} 
        onOpenChange={(open) => !open && setAutoTransaction(null)} 
        transaction={autoTransaction}
        walletName={wallets.find(w => w.id === autoTransaction?.fromWallet)?.name}
      />
      {isLoading && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white p-6 rounded-lg">
            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600 mx-auto"></div>
            <p className="mt-2 text-center">جاري تحميل البيانات...</p>
          </div>
        </div>
      )}
      
      <div className="max-w-7xl mx-auto space-y-6">
        {/* Header */}
        <div className="flex justify-between items-center">
          <h1 className="text-3xl font-bold text-gray-900">لوحة تحكم الإدارة</h1>
          <div className="flex items-center gap-2">
            <Button onClick={refreshDashboardData} variant="outline" className="flex items-center gap-2">
              <RefreshCcw className="h-4 w-4" />
              تحديث
            </Button>
            <Button onClick={signOut} variant="outline" className="flex items-center gap-2">
              <LogOut className="h-4 w-4" />
              تسجيل الخروج
            </Button>
          </div>
        </div>

        {/* Stats Cards */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">الرصيد النقدي</CardTitle>
              <DollarSign className="h-4 w-4 text-muted-foreground" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{cashBalance.toLocaleString('en-US', { maximumFractionDigits: 0 })} جنيه</div>
            </CardContent>
          </Card>
          
          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">أرصدة المحافظ</CardTitle>
              <Wallet className="h-4 w-4 text-muted-foreground" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">
                {Object.values(walletBalances).reduce((sum, balance) => sum + balance, 0).toLocaleString('en-US', { maximumFractionDigits: 0 })} جنيه
              </div>
            </CardContent>
          </Card>
          
          <Card className="relative">
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">المعاملات اليوم</CardTitle>
              <ArrowUpDown className="h-4 w-4 text-muted-foreground" />
            </CardHeader>
            <CardContent className={isDailyLocked ? "filter blur-sm pointer-events-none" : ""}>
              <div className="text-2xl font-bold">{transactions.length}</div>
            </CardContent>
            {isDailyLocked && (
              <div className="absolute inset-0 flex items-center justify-center rounded-lg bg-yellow-100/60">
                <Button
                  size="sm"
                  variant="ghost"
                  onClick={() => setIsDailyPinDialogOpen(true)}
                  className="flex items-center gap-2 bg-yellow-200/70 hover:bg-yellow-300 text-yellow-900 px-3 py-2 rounded-xl shadow-md hover:shadow-lg transition-all"
                  title="عرض تقارير اليوم"
                >
                  <Eye className="h-5 w-5" />
                  <span className="text-xs">عرض تقارير اليوم</span>
                </Button>
              </div>
            )}
          </Card>
          
          <Card className="relative">
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">إجمالي اليوم</CardTitle>
              <DollarSign className="h-4 w-4 text-muted-foreground" />
            </CardHeader>
            <CardContent className={isDailyLocked ? "filter blur-sm pointer-events-none" : ""}>
              <div className="text-2xl font-bold">
                {transactions.reduce((sum, t) => sum + t.amount, 0).toLocaleString()} جنيه
              </div>
            </CardContent>
            {isDailyLocked && (
              <div className="absolute inset-0 flex items-center justify-center rounded-lg bg-yellow-100/60">
                <Button
                  size="sm"
                  variant="ghost"
                  onClick={() => setIsDailyPinDialogOpen(true)}
                  className="flex items-center gap-2 bg-yellow-200/70 hover:bg-yellow-300 text-yellow-900 px-3 py-2 rounded-xl shadow-md hover:shadow-lg transition-all"
                  title="عرض تقارير اليوم"
                >
                  <Eye className="h-5 w-5" />
                  <span className="text-xs">عرض تقارير اليوم</span>
                </Button>
              </div>
            )}
          </Card>

          {/* Dialog to verify master PIN and unlock daily reports */}
          <MasterPinDialog
            open={isDailyPinDialogOpen}
            onOpenChange={setIsDailyPinDialogOpen}
            mode="verify"
            title="قفل تقارير اليوم"
            description="لا يمكن الاطلاع على تقارير اليوم إلا بإدخال الرقم السري الرئيسي"
            userId={user?.uid}
            userRole={profile?.role}
            onSuccess={() => {
              setIsDailyLocked(false);
              setIsDailyPinDialogOpen(false);
            }}
          />
        </div>

        {/* Transaction Form */}
        <Card className={isMobile && isTransactionPinned ? 'sticky top-0 z-40 shadow-lg bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/70' : ''}>
          <CardHeader>
            <CardTitle>إجراء معاملة جديدة</CardTitle>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <div>
                  <label className="block text-sm font-medium mb-2">نوع المعاملة</label>
                  <Select value={transactionType} onValueChange={(value: 'transfer' | 'withdraw') => setTransactionType(value)}>
                    <SelectTrigger>
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="transfer">تحويل</SelectItem>
                      <SelectItem value="withdraw">سحب</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
                
                <div>
                  <label className="block text-sm font-medium mb-2">رقم المرسل</label>
                  <Input
                    type="tel"
                    value={senderPhone}
                    onChange={(e) => setSenderPhone(e.target.value)}
                    onKeyDown={(e) => {
                      if (e.key === 'Enter') {
                        const dest = document.getElementById('receiverPhoneInput') as HTMLInputElement | null;
                        if (dest) {
                          dest.focus();
                        } else {
                          const amountEl = document.getElementById('amountInput') as HTMLInputElement | null;
                          amountEl?.focus();
                        }
                      }
                    }}
                    placeholder="01xxxxxxxxx"
                  />
                </div>
                
                {transactionType === 'transfer' && (
                  <div>
                    <label className="block text-sm font-medium mb-2">رقم المستقبل</label>
                    <Input
                      id="receiverPhoneInput"
                      type="tel"
                      value={receiverPhone}
                      onChange={(e) => setReceiverPhone(e.target.value)}
                      onKeyDown={(e) => {
                        if (e.key === 'Enter') {
                          const next = document.getElementById('amountInput') as HTMLInputElement | null;
                          next?.focus();
                        }
                      }}
                      placeholder="01xxxxxxxxx"
                    />
                  </div>
                )}
                {transactionType === 'withdraw' && (
                  <div>
                    <label className="block text-sm font-medium mb-2">اسم الراسل (اختياري)</label>
                    <Input
                      id="senderNameInput"
                      type="text"
                      value={senderName}
                      onChange={(e) => setSenderName(e.target.value)}
                      placeholder="مثال: أحمد عبد الرحمن"
                    />
                  </div>
                )}
                
                <div>
                  <label className="block text-sm font-medium mb-2">شركة المرسل</label>
                  <Select value={senderProvider} onValueChange={(v) => setSenderProvider(v as any)}>
                    <SelectTrigger>
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="vodafone_cash">فودافون</SelectItem>
                      <SelectItem value="etisalat_cash">اتصالات</SelectItem>
                      <SelectItem value="orange_cash">أورانج</SelectItem>
                      <SelectItem value="instapay">إنستاباي</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
                
                <div>
                  <label className="block text-sm font-medium mb-2">شركة المستقبل</label>
                  <Select value={receiverProvider} onValueChange={(v) => setReceiverProvider(v as any)}>
                    <SelectTrigger>
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="vodafone_cash">فودافون</SelectItem>
                      <SelectItem value="etisalat_cash">اتصالات</SelectItem>
                      <SelectItem value="orange_cash">أورانج</SelectItem>
                      <SelectItem value="instapay">إنستاباي</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
                
                <div>
                  <label className="block text-sm font-medium mb-2">المبلغ</label>
                  <Input
                    id="amountInput"
                    type="number"
                    value={amount}
                    onChange={(e) => setAmount(e.target.value)}
                    onKeyDown={(e) => {
                      if (e.key === 'Enter') {
                        const submitBtn = document.getElementById('txSubmitButton') as HTMLButtonElement | null;
                        submitBtn?.focus();
                      }
                    }}
                    placeholder="0.00"
                  />
                </div>
                {transactionType === 'withdraw' && (
                  <div>
                    <label className="block text-sm font-medium mb-2">اسم الراسل (اختياري)</label>
                    <Input
                      id="senderNameInput"
                      type="text"
                      value={senderName}
                      onChange={(e) => setSenderName(e.target.value)}
                      placeholder="مثال: أحمد عبد الرحمن"
                    />
                  </div>
                )}
                {transactionType === 'transfer' && ((normalizeDigits(senderPhone || '').replace(/\D/g, '').startsWith('010') && normalizeDigits(receiverPhone || '').replace(/\D/g, '').startsWith('010')) || (normalizeDigits(senderPhone || '').replace(/\D/g, '').startsWith('010') && (normalizeDigits(receiverPhone || '').replace(/\D/g, '').startsWith('011') || normalizeDigits(receiverPhone || '').replace(/\D/g, '').startsWith('012') || normalizeDigits(receiverPhone || '').replace(/\D/g, '').startsWith('015'))) || (normalizeDigits(senderPhone || '').replace(/\D/g, '').startsWith('012') && normalizeDigits(receiverPhone || '').replace(/\D/g, '').startsWith('010')) || (normalizeDigits(senderPhone || '').replace(/\D/g, '').startsWith('011') && normalizeDigits(receiverPhone || '').replace(/\D/g, '').startsWith('010')) || (normalizeDigits(senderPhone || '').replace(/\D/g, '').startsWith('015') && normalizeDigits(receiverPhone || '').replace(/\D/g, '').startsWith('010'))) && (
                  <div>
                    <label className="block text-sm font-medium mb-2">رسوم التحويل</label>
                    <Input
                      type="number"
                      value={transferFee}
                      onChange={(e) => setTransferFee(e.target.value)}
                      placeholder="0.00"
                    />
                    <p className="text-xs text-gray-500 mt-1">تُخصم من المحفظة وتضاف للدرج</p>
                  </div>
                )}
              </div>
              
              <Button id="txSubmitButton" type="submit" className={`w-full ${transactionType === 'transfer' ? 'btn-transfer-confirm' : 'btn-withdraw-confirm'}`}>
                {transactionType === 'transfer' ? 'إرسال التحويل' : 'تنفيذ السحب'}
              </Button>
            </form>
          </CardContent>
        </Card>

        {/* Transactions List */}
        <Card>
          <CardHeader>
            <CardTitle>المعاملات الأخيرة</CardTitle>
          </CardHeader>
          <CardContent>
            {transactions.length === 0 ? (
              <p className="text-sm text-gray-500">لا توجد معاملات حديثة.</p>
            ) : (
              <div className={(isMobile || isAndroid) ? "receipts-list space-y-3 max-h-[310px] overflow-y-auto pr-1 scrollbar-thin scrollbar-thumb-gray-300 scrollbar-track-gray-100 hover:scrollbar-thumb-gray-400" : "receipts-list space-y-3"}>
                {transactions.map((transaction) => (
                  <div 
                    key={transaction.id} 
                    className="relative flex items-center gap-4 p-4 rounded-xl bg-card border border-gray-100 shadow-sm hover:shadow-md transition-all duration-200"
                  >
                    {/* Icon Column */}
                    <div className={`h-12 w-12 rounded-full flex items-center justify-center shrink-0 ${
                       transaction.type === 'withdraw' ? 'bg-red-50' : 'bg-green-50'
                    }`}>
                       {transaction.type === 'withdraw' ? (
                          <ArrowDownLeft className="h-6 w-6 text-red-600" />
                       ) : (
                          <ArrowUpRight className="h-6 w-6 text-green-600" />
                       )}
                    </div>

                    {/* Content Column */}
                    <div className="flex-1 min-w-0">
                      <div className="flex items-center justify-between mb-1">
                        <h4 className="text-base font-bold text-foreground truncate">
                          {transaction.type === 'send' ? 'تحويل أموال' : 'سحب نقدي'}
                        </h4>
                        <span className={`text-base font-bold whitespace-nowrap ${
                          transaction.type === 'send' ? 'text-green-600' : 'text-red-600'
                        }`}>
                          {transaction.type === 'send' ? '-' : '+'} {transaction.amount.toLocaleString()} ج.م
                        </span>
                      </div>

                      <div className="flex items-center justify-between">
                         <div className="flex flex-col gap-0.5">
                            <p className="text-sm text-muted-foreground truncate">
                               {transaction.type === 'send' ? (
                                  <span dir="ltr">{transaction.receiverPhone}</span>
                               ) : (
                                  <span dir="ltr">{transaction.senderPhone}</span>
                               )}
                            </p>
                            <p className="text-sm font-medium text-gray-500 mt-0.5">
                               {transaction.createdAt instanceof Date ? transaction.createdAt.toLocaleString('ar-EG', { day: 'numeric', month: 'short', hour: 'numeric', minute: 'numeric', hour12: true }) : new Date((transaction.createdAt as any).seconds * 1000).toLocaleString('ar-EG', { day: 'numeric', month: 'short', hour: 'numeric', minute: 'numeric', hour12: true })}
                            </p>
                            {(transaction.fromWallet || transaction.shopName || transaction.provider) && (
                              <p className="text-xs text-blue-600 mt-1 flex items-center gap-1">
                                <Wallet className="w-3 h-3" />
                                {wallets.find(w => w.id === transaction.fromWallet)?.name || 
                                 (transaction.provider === 'vodafone_cash' ? 'فودافون كاش' : 
                                  transaction.provider === 'etisalat_cash' ? 'اتصالات كاش' : 
                                  transaction.provider === 'orange_cash' ? 'أورانج كاش' : 
                                  transaction.provider === 'instapay' ? 'إنستاباي' : 
                                  transaction.shopName || 'المحفظة')}
                              </p>
                            )}
                         </div>

                         <div className={`px-2.5 py-1 rounded-full text-xs font-medium flex items-center gap-1 ${
                            transaction.status === 'completed' 
                              ? 'bg-green-50 text-green-700' 
                              : transaction.status === 'pending'
                              ? 'bg-yellow-50 text-yellow-700'
                              : 'bg-red-50 text-red-700'
                         }`}>
                            {transaction.status === 'completed' && <Check className="h-3 w-3" />}
                            {transaction.status === 'pending' && <Clock className="h-3 w-3" />}
                            {transaction.status === 'failed' && <X className="h-3 w-3" />}
                            <span>
                               {transaction.status === 'completed' ? 'ناجحة' : transaction.status === 'pending' ? 'معالجة' : 'فشلت'}
                            </span>
                         </div>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </CardContent>
        </Card>
        
        <AutoTransactionDialog 
          open={!!autoTransaction} 
          onOpenChange={(open) => !open && setAutoTransaction(null)} 
          transaction={autoTransaction}
          walletName={wallets.find(w => w.id === autoTransaction?.fromWallet)?.name || 
            (autoTransaction?.provider === 'vodafone_cash' ? 'فودافون كاش' : 
             autoTransaction?.provider === 'etisalat_cash' ? 'اتصالات كاش' : 
             autoTransaction?.provider === 'orange_cash' ? 'أورانج كاش' : 
             autoTransaction?.provider === 'instapay' ? 'إنستاباي' : 
             autoTransaction?.shopName)}
        />
      </div>
    </div>
  );
};

export default DashboardPage;

// كشف الشركة تلقائيًا من مقدمة الرقم
const detectProvider = (phone: string): 'vodafone_cash' | 'etisalat_cash' | 'orange_cash' | 'instapay' | null => {
  const digits = normalizeDigits(phone).replace(/\D/g, '');
  if (digits.startsWith('010')) return 'vodafone_cash';
  if (digits.startsWith('011')) return 'etisalat_cash';
  if (digits.startsWith('012')) return 'orange_cash';
  // مبدئيًا لا ندعم 015 (WE) هنا؛ تظل القيمة كما هي أو يُستخدم الاختيار اليدوي
  return null;
};
